.header-container {
    display: flex;
    height: 100vh;
    width: 100vw;
    position: relative;
    align-items: center;
    justify-content: center;
  }

.header-canvas {
    background-image: linear-gradient(rgb(0, 5, 40), rgb(3,37,126));
    position: absolute;
}

.stars-container {
  width: 100vw;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.shooting-star {
    position: absolute;
    background-color: rgba(254, 234, 0, 0.5);
    border-radius: 50%;
    width: 7px;
    height: 7px;
    z-index: 0;
}

.header-text {
    position: absolute;
    font-size: 5vw;
    margin: auto;
    color: white;
    background-color: rgba(118, 146, 255, 0.25);
    border-radius: 15px;
    padding: 10px 40px 10px 40px;
    z-index: 1;
    text-align: center;
  }
  
  @media only screen and (max-width: 800px) {
    .header-text {
      padding: 10px;
      font-size: 8vw;
    }
  }